const apikey = 'qakMad23d7r8z2DAQbI5YIp0Yx45N7dp'


//const apiurl = 'https://api.giphy.com/v1/gifs/search?api_key=qakMad23d7r8z2DAQbI5YIp0Yx45N7dp&q=culos&limit=25&offset=0&rating=g&lang=en';
export default function getGif ({keyword='morty'} = {}){
    
    const apiurl = `https://api.giphy.com/v1/gifs/search?api_key=${apikey}&q=${keyword}&limit=25&offset=0&rating=g&lang=en`;

    return fetch(apiurl)
    .then(res => res.json())
    .then(response => 
      {const {data}= response
      const giftss = data.map(image=> {
        const {images, title, id} =  image
         const {url} =  images.downsized_medium
         return {title, id, url}
        })
        return giftss
    })
}